@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600&display=swap");
.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.section {
  padding: 2rem 0 4rem;
  height: 100vh;
}

.section__title {
  font-size: var(--h1-font-size);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-3);
}

.section__title,
.section__subtitle {
  text-align: center;
}

.trim-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.card {
  padding: 0;
  margin: 0.9375rem 0.625rem;
  border-radius: var(--card-border-radius);
  overflow: hidden;
  position: relative;
}
.card--xxsmall {
  grid-row-end: span var(--card-xxsmall);
}
.card--xsmall {
  grid-row-end: span var(--card-xsmall);
}
.card--small {
  grid-row-end: span var(--card-small);
}
.card--medium {
  grid-row-end: span var(--card-medium);
}
.card--large {
  grid-row-end: span var(--card-large);
}
.card__title {
  width: 0;
  height: 0;
  opacity: 0;
}
.card__image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.card__image {
  height: 100%;
  width: 100%;
  border-radius: 16px;
  display: block;
  object-fit: cover;
}
.modal {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  background-color: rgba(0, 0, 0, 0.1);
}
.modal:hover {
  opacity: 1;
}
.modal__profile {
  color: white;
  font-size: var(--m-font-size);
  font-weight: var(--font-semi-bold);
  padding: 0.9375rem 0.9375rem;
  cursor: pointer;
}
.modal__profile-icon {
  margin-left: 0.3125rem;
}
.modal__head {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  height: 30%;
  position: fixed;
  top: 0;
  padding: 0.625rem 0.625rem;
}
.modal__save {
  border-radius: 1.375rem;
  background-color: red;
  font-size: var(--m-font-size);
  font-weight: var(--font-semi-bold);
  color: white;
  cursor: pointer;
}
.modal__save:hover {
  background-color: #ad081b;
}
.modal__save-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.9375rem 0.9375rem;
}
.modal__foot {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  width: 100%;
  height: 15%;
  position: fixed;
  bottom: 0;
  padding: 0 0.625rem 0.625rem;
  font-weight: 500;
}
.modal__destination {
  display: flex;
  width: 60%;
}
.modal__mock-icon-container {
  display: flex;
  background-color: #f0f0f0;
  padding: 0.5rem;
  border-radius: 1.375rem;
  cursor: pointer;
}
.modal__mock-icon-container:hover {
  background-color: white;
}
.modal__mock-text {
  font-size: var(--m-font-size);
  font-weight: 600;
}
.modal__mock-icon {
  padding: 0 0.5rem;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}
.modal__social {
  display: flex;
  justify-content: flex-end;
  column-gap: 0.5rem;
  width: 40%;
}

:root {
  --card-width: 250px;
  --card-border-radius: 16px;
  --row-increment: 10px;
  --card-xxsmall: 13;
  --card-xsmall: 20;
  --card-small: 26;
  --card-medium: 33;
  --card-large: 45;
}

.pin__container {
  margin: 5rem 0 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, var(--card-width));
  grid-auto-rows: var(--row-increment);
}

.main {
  background-color: #f0f0f0;
  width: 100%;
  height: calc(100% - 80px);
  position: relative;
  top: 5rem;
}
.main__wrapper {
  padding: 2.5rem 0;
  width: 100%;
  height: 100%;
}
.main__pin-nav {
  display: flex;
  column-gap: 1.5rem;
  font-size: var(--m-font-size);
  font-weight: var(--font-semi-bold);
  position: absolute;
  top: 5rem;
}
.main__btn {
  border-radius: 0.5rem;
  cursor: pointer;
  padding: 0.5rem;
  position: relative;
}
.main__btn-active::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  width: 80%;
  transform: translate(-50%, -50%);
  border-bottom: 3px solid black;
}
.main__save:hover {
  background-color: #dadada;
}

.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 9rem;
  left: 50%;
  transform: translate(-50%);
}

.main__input {
  position: absolute;
  bottom: 1rem;
  right: 0.5rem;
}

.add {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 2.5rem 0;
  background-color: #fff;
  width: 55rem;
  min-width: 55rem;
  height: 40.625rem;
  border-radius: 1rem;
  padding: 2.5rem 2.5rem 0;
}
.add__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
}
.add__ellipsis {
  background-color: white;
  padding: 0.75rem;
  border-radius: 50%;
  cursor: pointer;
  color: #757575;
  transition: all 0.2s ease;
}
.add__ellipsis:hover {
  background-color: #f0f0f0;
}
.add__board {
  display: flex;
}
.add__select-board-container {
  display: flex;
  position: relative;
}
.add__select-board {
  display: flex;
  justify-content: space-between;
  height: 2.5rem;
  align-items: center;
  background-color: #efefef;
  cursor: pointer;
  width: 12.5rem;
  height: 2.5rem;
  padding: 0px 0.625rem;
  border-radius: 0.5rem 0px 0px 0.5rem;
}
.add__arrow-down {
  pointer-events: none;
  position: absolute;
  right: 0.6rem;
  top: 50%;
  transform: translate(0, -50%);
}
.add__save {
  color: white;
  font-weight: var(--font-semi-bold);
  background-color: #e60023;
  cursor: pointer;
  height: 2.5rem;
  padding: 0 0.875rem;
  border-radius: 0px 8px 8px 0px;
}

.side__container {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 0 2.5rem;
}

.left-side {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 48%;
  border-radius: 0.5rem;
  padding: 0 1.25rem;
}
.left-side__img-label {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: #efefef;
  border-radius: 0.5rem;
}
.left-side__input {
  width: 0;
  height: 0;
  opacity: 0;
}
.left-side__img-container {
  padding: 0.625rem;
  width: 100%;
  height: 100%;
}
.left-side__dotted-border {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border: 2px dashed #dadada;
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  position: relative;
}
.left-side__mock-icon-container {
  background-color: transparent;
  color: #767676;
  font-size: 2.1rem;
  padding: 1.5rem;
}
.left-side__upload-text {
  text-align: center;
  padding: 0 3rem;
}
.left-side__recommend-size {
  text-align: center;
  position: absolute;
  bottom: 1.5rem;
  font-size: var(--s-font-size);
  color: #6c6c6c;
  padding: 0 1.1rem;
}
.left-side__image-container {
  max-width: 21.5rem;
  max-height: 31.875rem;
  display: flex;
  padding: 0;
  border-radius: var(--card-border-radius);
  overflow: hidden;
  display: none;
  opacity: 0;
}
.left-side__image-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden !important;
}
.left-side .pin_max_width {
  max-width: 100%;
}
.left-side .pin_max_height {
  max-height: 100%;
}

.right-side {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 52%;
  height: 100%;
  padding: 0 1.25rem;
}
.right-side__add-info {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.right-side__title, .right-side__description, .right-side__destination {
  width: 100%;
  background-color: transparent;
  outline: none;
  border-bottom: 1px solid #dadada;
  white-space: pre-wrap !important;
  overflow: hidden;
}
.right-side__title:focus, .right-side__description:focus, .right-side__destination:focus {
  border-bottom: 1px solid #0074e8;
}
.right-side__title {
  font-size: var(--l-font-size);
  height: 4.0625rem;
  color: gray;
  padding-bottom: 2rem;
  margin-top: 2rem;
}
.right-side__user {
  display: flex;
  margin-top: 2rem;
}
.right-side__user-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  background-color: #efefef;
  border-radius: 50%;
  font-weight: 700;
}
.right-side__user-circle:hover {
  background-color: #e6e6e6;
}
.right-side__user-name {
  display: flex;
  align-items: center;
  padding-left: 0.5rem;
  font-weight: 700;
}
.right-side__description {
  height: 2.5rem;
  margin-top: 1rem;
  font-size: var(--ml-font-size);
}
.right-side__alt-text-container {
  margin-top: 1.5rem;
  min-width: 3.75rem;
  min-height: 2.5rem;
  width: max-content;
  display: inline-block;
  background-color: #efefef;
  border-radius: 1.5rem;
  cursor: pointer;
}
.right-side__alt-text-container:hover {
  background-color: #e6e6e6;
}
.right-side__alt-text {
  font-weight: 700;
  display: inline-block;
  padding: 0.5rem 0.75rem;
  text-align: center;
}
.right-side__destination-container {
  display: flex;
  height: 40%;
}
.right-side__destination {
  align-self: flex-end;
  padding-bottom: 0.7rem;
  font-size: var(--sm-font-size);
  color: #919090;
  margin-bottom: 0.5rem;
}

.main {
  display: none;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}

.add-pin__navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-height: 2.5rem;
  min-width: 11.25rem;
  max-height: 90vh;
  max-width: max-content;
  width: 100%;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: var(--z-modal);
  transition: all 0.2s ease-in-out;
}
.add-pin__image-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem;
  width: 3.5rem;
  height: 3.5rem;
  font-size: 1.35rem;
  background-color: #ffffff;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transition: all 0.2s ease-in-out;
  position: relative;
}
.add-pin__image-btn:hover {
  background-color: #f0f0f0;
}
.add-pin__image-icon {
  pointer-events: none;
}
.add-pin__image-input {
  display: none;
}
.add-pin__msg {
  background-color: #fff;
  border: 1px solid white;
  width: 18.75rem;
  position: absolute;
  top: -1rem;
  right: 5rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 1rem;
  display: none;
  overflow: hidden;
}
.add-pin__browser {
  padding: 0.25rem;
}
.add-pin__upload-pin {
  padding: 0.25rem;
}
.add-pin__upload-pin-container {
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-weight: 800;
}
.add-pin__upload-pin-container:hover {
  background-color: #efefef;
}

.upload__icon {
  padding: 0.25rem;
  font-size: 1.1rem;
}

.random__hr {
  color: #dadada;
}

.menuOff {
  display: none;
}

.menuOn {
  display: block;
}

.nav {
  height: 5rem;
  width: 100%;
  min-width: 20rem;
  position: fixed;
  top: 0;
  z-index: 11111;
  background-color: white;
}
.nav__wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0.5rem 1rem;
}
.nav__inner {
  display: flex;
  height: 3rem;
  width: 100%;
}
.nav__left-side {
  display: flex;
  align-items: center;
}
.nav__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  width: 3rem;
  font-size: 1.5rem;
  border-radius: 50%;
}
.nav__logo:hover {
  background-color: #ececec;
}
.nav__home {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1.5rem;
  height: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
  font-weight: 800;
  font-size: var(--lm-font-size);
}
.nav__home:hover {
  background-color: #ececec;
}
.nav__search {
  margin: 0 0.5rem;
  padding: 0 0 0 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #efefef;
  border-radius: 1.5rem;
}
.nav__search-icon {
  padding-left: 1rem;
  color: #767676;
}
.nav__search-inner {
  padding: 0 1rem;
  background-color: #efefef;
  border-radius: 1.5rem;
  vertical-align: middle;
  height: 100%;
  width: 100%;
  margin-left: 0.5rem;
  font-size: 1rem;
  overflow: hidden;
  opacity: 0.6;
}
.nav__right-side {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__socials {
  display: flex;
  font-size: 1.5rem;
}
.nav__bell {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #767676;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}
.nav__bell:hover {
  background-color: #ececec;
}
.nav__communication {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #767676;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}
.nav__communication:hover {
  background-color: #ececec;
}
.nav__personal {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__saved {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}
.nav__saved:hover {
  background-color: #ececec;
}
.nav__saved-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: var(--s-font-size);
  border-radius: 50%;
  background-color: #efefef;
  width: 1.5rem;
  height: 1.5rem;
  font-weight: var(--font-semi-bold);
}
.nav__account {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  color: #767676;
  cursor: pointer;
}
.nav__account:hover {
  background-color: #ececec;
}

.account {
  position: absolute;
  right: 0;
  top: 4rem;
  background-color: white;
  min-height: 2.5rem;
  min-width: 11.25rem;
  max-height: 90vh;
  max-width: 90vw;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.account__top {
  padding: 0.5rem;
}
.account__logged {
  font-weight: 400;
  font-size: var(--s-font-size);
  padding: 0.5rem 0.5rem;
}
.account__personal {
  display: flex;
  flex: 1 1 auto;
  border-radius: 0.5rem;
  padding: 0.5rem 0.5rem;
  cursor: pointer;
}
.account__personal:hover {
  background-color: #efefef;
}
.account__photo {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: var(--font-semi-bold);
  background-color: #efefef;
  border-radius: 50%;
  width: 3.75rem;
  height: 3.75rem;
  margin-right: 0.5rem;
}
.account__information {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 0.5rem;
  word-break: break-word;
}
.account__name {
  font-size: var(--m-font-size);
  font-weight: 800;
}
.account__type {
  color: #767676;
  font-size: var(--s-font-size);
}
.account__mail {
  color: #767676;
  font-size: var(--s-font-size);
}
.account__verfication {
  display: flex;
  align-items: center;
}
.account__middle {
  padding: 0.5rem;
}
.account__change-heading {
  font-weight: 400;
  font-size: var(--s-font-size);
  padding: 0.5rem 0.5rem;
}
.account__add {
  display: flex;
  flex-direction: column;
  font-weight: 800;
  cursor: pointer;
}
.account__add-inner {
  display: flex;
  align-items: center;
  min-height: 2.1875rem;
  border-radius: 0.5rem;
  padding: 0 0.5rem;
}
.account__add-inner:hover {
  background-color: #efefef;
}
.account__convert-inner {
  display: flex;
  align-items: center;
  min-height: 2.1875rem;
  border-radius: 0.5rem;
  padding: 0 0.5rem;
}
.account__convert-inner:hover {
  background-color: #efefef;
}
.account__bottom {
  padding: 0.5rem;
}
.account__bottom-heading {
  font-weight: 400;
  font-size: var(--s-font-size);
  padding: 0.5rem 0.5rem;
}
.account__options {
  display: flex;
  flex-direction: column;
  font-weight: 800;
  cursor: pointer;
}
.account__option {
  display: flex;
  align-items: center;
  min-height: 2.1875rem;
  border-radius: 0.5rem;
  padding: 0 0.5rem;
}
.account__option:hover {
  background-color: #efefef;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font: inherit;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  overflow-y: scroll;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

select {
  border: none;
  font: inherit;
  box-sizing: inherit;
  width: 100%;
  background-color: inherit;
  appearance: none;
}

input {
  text-decoration: none;
  border: none;
}

:root {
  --hue-color: 215;
  --saturation: 82%;
  --lightness: 99%;
  --primary-clr-300: hsl(215, 82%, 61%);
  --primary-clr-400: hsl(215, 82%, 51%);
  --primary-clr-500: hsl(215, 82%, 41%);
  --primary-clr-600: hsl(215, 82%, 31%);
  --secondary-clr-300: hsl(215, 57%, 53%);
  --first-color-lighter: hsl(215, 92%, 85%);
  --title-color: hsl(215, 8%, 15%);
  --text-color: hsl(215, 8%, 45%);
  --text-color-light: hsl(215, 8%, 65%);
  --input-color: hsl(215, 70%, 96%);
  --input-color-darken: hsl(215, 70%, 90%);
  --body-color: hsl(215, 10%, 98%);
  --header-color: hsl(215, 67%, 95%);
  --container-color: #fff;
  --scroll-bar-color: hsl(215, 12%, 90%);
  --scroll-thumb-color: hsl(215, 37%, 31%);
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --header-color: hsl(var(--hue-color), 67%, 7%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

:root {
  --body-font: "Quicksand", sans-serif;
}

.container {
  max-width: 55rem;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

:root {
  --header-height: 3rem;
  --xs-font-size: 0.75rem;
  --s-font-size: 0.813rem;
  --m-font-size: 0.938rem;
  --ml-font-size: 0.95rem;
  --sm-font-size: 1.1rem;
  --l-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tolltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
@media (min-width: 71.875em) {
  :root {
    --xs-font-size: 0.813rem;
    --s-font-size: 0.875rem;
    --m-font-size: 1rem;
    --l-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  box-shadow: 0;
  border: 0;
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1;
  /* 1 */
  margin: 0;
  /* 2 */
}

textarea {
  resize: none;
}